import React from "react";
import "./Todo.css";

const Todo = ({ list, onRemove }) => {
  return (
    <div className="todo-main">
      <ul>
        {list.map((item) => (
          <li key={item.id}>
            <span>{item.firstname}</span>
            <button onClick={() => onRemove(item.id)}>
              {item.isCompleted}
            </button>
          </li>
        ))}
      </ul>
    </div>
  );
};

export default Todo;
