import "./styles.css";
import React from "react";

import Todo from "./todo/Todo";

const initialList = [
  {
    id: "a",
    firstname: "Robin",
    lastname: "Wieruch",
    isCompleted: "done",
    year: 1988
  },
  {
    id: "b",
    firstname: "Dave",
    lastname: "Davidds",
    isCompleted: "done",
    year: 1990
  }
];
export default function App() {
  const [list, setList] = React.useState(initialList);

  const handleRemove = (id) => {
    const newList = list.filter((item) => item.id !== id);
    setList(newList);
  };
  return (
    <div className="App">
      <Todo list={list} onRemove={handleRemove} />
    </div>
  );
}
